.Giphy {
  text-align: center;
  background-color: black;
}

.neonText{
 
    color: #fff;
    text-shadow:
        0 0 7px #fff,
        0 0 7px #fff,
        0 0 1px #fff,
        0 0 3px rgb(255, 94, 0),
        0 0 6px rgb(255, 0, 0),
        0 0 72px #0fa,
        0 0 8px #0fa,
        0 0 1px #0fa;
}

h1{
  font-size: 2.2rem;
  animation: flicker 1.5s infinite alternate; 
}



@keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #0fa,
      0 0 80px #0fa,
      0 0 90px #0fa,
      0 0 100px #0fa,
      0 0 150px #0fa;
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}


